import {
	mapState,
	mapMutations
} from 'vuex';
import {
	wechatSyncInvoice,
	indexQueryData,
	getRiseInfo
	// copyRiseInfo
} from '@/service/service.js';
import {
	ActionPzsp,
	ActionSmcy,
	ActionWxfp,
	ActionXzbx,
	ActionJcdl,
	ActionDksp
} from '@/utils/actions.js';
import store from '@/store/index.js';

export default {
	data() {
		return {
			// // 报销模块
			// reimburse: {
			// 	auditingCount: 0,
			// 	waitingTicketscount: 0,
			// 	waitingPaymentCount: 0,
			// 	paidCount: 0,
			// 	rejectedCount: 0
			// },
			autoplay: true,
			interval: 3000,
			duration: 1000,
			isRise: true,
			rise: []
		};
	},
	onLoad() {
		let that = this;
		console.log('首页加载..');
		uni.$on('LOGIN_SUCCESS', function(data) {
			console.log('监听到【LOGIN_SUCCESS】事件..');
			that.queryData();
		});
	},
	onShow() {
		console.log('首页显示..');
		this.queryData();
		
		this.showRiseInfo(); //获取抬头
	},
	// 下拉刷新信息
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
		console.log('下拉刷新首页信息..');
		this.queryData(function() {
			uni.stopPullDownRefresh();
		});
	},
	computed: {
		reimburse: function() {
			return store.state.home.reimburse;
		},
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		// 进入抬头详情
		handleRiseDetail(index) {
			let rise_data = this.rise[index];
			rise_data = JSON.stringify(rise_data);
			console.log(this.rise[index])
			uni.navigateTo({
				url: '../../rise/invoice-detail_tt?rise_data=' + rise_data
			});
		},
		// 新增抬头
		handleAddInvoice() {
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			uni.navigateTo({
				url: '../../rise/invoice-add_tt'
			})
		},
		//复制功能
		copyRiseInfo() {
			let info = this.rise[0];
			let str = info.title + '\n' + info.companyTaxNo;
			uni.setClipboardData({
				data: str,
				success: function() {
					// console.log('success');
					uni.showToast({
						title: "已复制到剪切板",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		//获取抬头
		showRiseInfo() {
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			getRiseInfo().then((res) => {
				// console.log(res);
				if (res.statusCode === 1) {
					this.rise = res.data.slice(0, 1);
					// console.log(this.rise)
					if(this.rise.length > 0) {
						console.log(this.rise)
						this.isRise = false;
					} else {
						this.isRise = true;
					}
				}
			})
		},
		//跳转更多抬头
		handleRise() {
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			uni.navigateTo({
				url: `../../rise/invoice-rise_tt`
			})
		},
		//滴滴跳转 
		handleDD() {
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			uni.navigateTo({
				url: `../../dd/dd`
			});
		},
		//票夹跳转
		handleWallet() {
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			uni.navigateTo({
				url: '../ticket/ticket_tt'
			});
		},
		//跳转发票抬头
		handleTinvoice() {
			uni.navigateTo({
				url: '../../rise/invoice-rise_tt'
			});
		},
		// 抵扣收票
		handleDksp() {
			console.log('抵扣收票..');
			ActionDksp();
		},
		// 微信发票
		handleWxfp() {
			console.log('微信发票..');
			ActionWxfp();
		},
		// 发票查验
		handleFpcy() {
			console.log('发票查验..');
			ActionSmcy();
		},
		// 扫码收票
		handleSmsp() {
			console.log('扫码收票..');
			ActionSmcy();
		},
		// 拍照识票
		// handlePzsp() {
		// 	console.log('拍照收票..');
		// 	ActionPzsp();
		// },
		//拍照收票
		handlePzsp() {
			ActionPzsp();
		},
		// 新增报销
		handleXzbx() {
			console.log('新增报销..');
			ActionXzbx();
		},
		// 处理待审核的报销单据
		handleDshReceipt(audit) {
			console.log('跳转至待审核的报销单据..');
			let dl = ActionJcdl();
			if (!dl) {
				return;
			}
			store.state.reimburse.audit = audit;
			uni.switchTab({
				url: "../reimburse/reimburse",
				success() {
					let page = getCurrentPages().pop();
					//跳转页面成功之后           
					if (!page) return;
					page.onLoad(); //如果页面存在，则重新刷新页面	
				}
			});
		},
		queryData(callback) {
			let that = this;
			console.log('请求查询首页信息..');
			indexQueryData().then((qdRes) => {
				console.log('查询首页信息结果..', qdRes);
				if (callback) callback();
				if (qdRes && qdRes.success) {
					let rdata = qdRes.data;
					if (rdata) {
						let mdata = rdata.data;
						if (mdata) {
							store.state.home.reimburse = mdata;
						}
					}
				}
			});
		}
	}
}
